<template>
    <div id="order">
        <header-bar>
            <div class="btn" slot="btn">
                <img @click="()=>{
                    this.$router.push('/home')
                }" src="@/assets/images/Btn/首页-Btn.png">
            </div>
            <div class="sel-bt" slot="bt">
                <div v-for="item in order" :key="item"
                v-text="item" :class="isOrder==item?'active':''"
                @click="()=>{
                    isOrder=item
                }"
                ></div>
            </div>
        </header-bar>
        <div class="order-box">
            <template v-if="isOrder=='外卖订单'">
                <div class="order-sel">
                    <div v-for="(item,index) in orSels" :key="item"
                    @click="()=>{orSel = index}">
                        <p v-text="item"></p>
                    </div>
                    <div class="block" key="block1"></div>
                </div>
                <div class="null">
                    <p>
                        今天还没有下单
                        <br/>    
                        快去选择喜欢的商品吧
                    </p>
                    <button 
                    @click="()=>{this.$router.push('/indent')}">
                        去点单
                    </button>
                </div>
            </template>
            <template v-else>
                <div class="od-sel">
                    <div v-for="(item,index) in odSels" :key="item" 
                    v-text="item" @click="()=>{
                        odSel = index
                    }"></div>
                    <div class="od-btn">
                        <img src="@/assets/images/order/餐盘盖.png">
                        <p>堂食</p>
                    </div>
                    <div class="block" key="block2"></div>
                </div>
                <div class="null">
                    <div class="null-sel" v-if="odSel==0">
                        <div v-for="(item,index) in nuSels" 
                        :key="item"
                        :class="index==nuSel?'active':''"
                        @click="()=>{nuSel=index}"
                        v-text="item"></div>
                    </div>
                    <p>
                        今天还没有下单
                        <br/>    
                        快去选择喜欢的商品吧
                    </p>
                    <button 
                    @click="()=>{this.$router.push('/indent')}">
                        去点单
                    </button>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
import HeaderBar from '@/components/HeaderBar'

export default {
    name:'Order',
    components:{
        HeaderBar
    },
    data(){
        return{
            isOrder:'外卖订单',
            order:['外卖订单','历史订单'],
            orSel:0,
            orSels:['当前订单','历史订单'],
            odSel:0,
            odSels:['快餐','当面付','排队','预约'],
            nuSel:0,
            nuSels:['全部','制作中','已完成'],
        }
    }
}
</script>

<style lang="scss" scoped>
#order{
    padding-top: 100rem;
    height: calc(100% - 100rem);
    display: flex;
    flex-direction: column;
    .order-box{
        flex: 1;
        overflow-y: auto;
        overflow-x: hidden;
        background-color: #f5f5f5;
    }
}
.btn{
    width: 60rem;
    height: 60rem;
    border-radius: 30rem;
    border: 1rem solid #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    div{
        width: 1rem;
        height: 46rem;
        background-color: #e7e7e7;
    }
    img{
        width: 40rem;
    }
    img:last-child{
        width: 40rem;
    }
}
.sel-bt{
    font-size: 30rem;
    line-height: 30rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #eeeeee;
    height: 80rem;
    border-radius: 10rem;
    padding: 10rem;
    box-sizing: border-box;
    div{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        border-radius: 10rem;
    }
    div.active{
        background-color: white;
    }
}
.order-sel{
    &{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-around;
        height: 100rem;
        background-color: white;
    }
    div{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 36rem;
        line-height: 36rem;
        width: 100%;
        text-align: center;
    }
    .block{
        width: 100rem;
        height: 8rem;
        background-color: #dba86f;
        position: absolute;
        bottom: 0;
        left: calc(100% / 2 / 2 + 100% / 2 * v-bind(orSel));
        transform: translateX(-50%);
        transition: 0.3s;
    }
    
}
.od-sel{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 36rem;
    background-color: white;
    div{
        height: 100rem;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
    .od-btn{
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-right: 20rem;
        position: relative;
        img{
            width: 40rem;
        }
        &::after{
            display: block;
            content: '';
            width: 1rem;
            height: 50rem;
            background-color: #e7e7e7;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    .block{
        width: 100rem;
        height: 6rem;
        background-color: #dba86f;
        position: absolute;
        bottom: 0;
        left: calc(100% / 5 / 2 + 100% / 5 * v-bind(odSel));
        transform: translateX(-50%);
        transition: 0.3s;
    }
}
.null{
    flex: 1;
    
    text-align: center;
    p{
        margin-top: 420rem;
        font-size: 32rem;
        line-height: 50rem;
        color: #aaaaaa;
    }
    button{
        margin-top: 100rem;
        width: 440rem;
        height: 110rem;
        background-color: #dba86f;
        border: none;
        border-radius: 10rem;
        color: white;
        font-size: 40rem;
        line-height: 40rem;
    }
    .null-sel{
        margin-top: 20rem;
        margin-left: 20rem;
        width: 480rem;
        font-size: 32rem;
        line-height: 32rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        div{
            height: 80rem;
            border-radius: 40rem;
            padding: 0 30rem;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        div.active{
            background-color: #dba86f;
            color: white;
        }
    }
}
</style>